/*
 * rxButton
 */
@import "vars";

.button {
  display: inline-block;
  background: @rxButton-default-background-color;
  color: @rxButton-default-text-color;
  border: 0;
  font-size: 14px;
  padding: 7px 13px;
  margin-bottom: 10px;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.320, 1.275);

  &:hover,
  &:focus {
    background: @rxButton-default-hover-background-color;
    cursor: pointer;
    // in case the buttons are created using the <a> tag
    color: @rxButton-default-text-color;
    text-decoration: none;
  }

  // Size Variations
  &.xl {
    font-size: 16px;
    padding: 20px 30px;
  }

  &.lg {
    font-size: 15px;
    padding: 15px 26px;
  }

  &.sm {
    padding: 5px 10px;
    font-size: 13px;
  }

  &.xs {
    padding: 3px 7px;
    margin-bottom: 5px;
    font-size: 12px;
  }

  // Color Variations
  &.positive,
  &.submit {
    background: @rxButton-positive-background-color;

    &:hover,
    &:focus {
      background: @rxButton-positive-hover-background-color;
    }
  }

  &.negative {
    background: @rxButton-negative-background-color;

    &:hover,
    &:focus {
      background: @rxButton-negative-hover-background-color;
    }
  }

  &.finish {
    background: @rxButton-finish-background-color;
    color: @rxButton-finish-text-color;
    // Inset line is created with .box-shadow instead of border property, because it avoids changing button height.
    .box-shadow(inset 0 0 0 2px @rxButton-finish-outline-color);

    &:hover,
    &:focus {
      color: @rxButton-finish-hover-text-color;
      .box-shadow(inset 0 0 0 2px @rxButton-finish-hover-outline-color);
    }
  }

  &.cancel {
    background: @rxButton-cancel-background-color;

    &:hover,
    &:focus {
      background: @rxButton-cancel-hover-background-color;
    }
  }

  // Disabled styles should always come last in the cascade for color
  // variations styles, or else they won't override other colors properly.
  &[disabled],
  &[disabled]:hover {
    cursor: not-allowed;
    color: @rxButton-disabled-text-color;
    background: @rxButton-disabled-background-color;
  }

  &.inline {
    vertical-align: middle;
  }

  // Spinner styles
  .spinner {
    display: inline-block;
    position: relative;
    left: 3px;

    i {
      width: 5px;
      height: 5px;
      background: @rxButton-spinner-background-color;

      .border-radius(100%);
      display: inline-block;
      -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
      animation: bouncedelay 1.4s infinite ease-in-out;
      /* Prevent first frame from flickering when animation starts */
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;

      &.pos1 {
        -webkit-animation-delay: -0.32s;
        animation-delay: -0.32s;
      }

      &.pos2 {
        -webkit-animation-delay: -0.16s;
        animation-delay: -0.16s;
      }
    }
  }

  @-webkit-keyframes bouncedelay {
    0%,
    80%,
    100% {
      -webkit-transform: scale(0.0);
    }
    40% {
      -webkit-transform: scale(1.0);
    }
  }

  @keyframes bouncedelay {
    0%,
    80%,
    100% {
      transform: scale(0.0);
      -webkit-transform: scale(0.0);
    }
    40% {
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
    }
  }
}//.button

// Make buttons look like plain links
.btn-link {
  color: @app-link-text-color;
  background: none;
  border: 0;
  padding: 0;
  cursor: pointer;
  font-size: inherit;

  &:hover,
  &:focus {
    text-decoration: underline;
  }

  &[disabled],
  &[disabled]:hover {
    cursor: not-allowed;
    color: @rxButton-disabled-background-color;
  }
}

thead th .btn-link {
  color: @app-table-header-text-color;
  &:hover,
  &:focus {
    color: @app-table-header-text-color;
  }
}

.button-group {
  display: flex;
  border: @rxButton-button-group-border;
  border-radius: @rxButton-button-group-border-radius + 2;
  font-size: 1.1em;

  input {
    display: none;
  }

  label {
    flex: 1;
    text-align: center;
    padding: 7px 13px;
    color: @gray-700;

    &:first-of-type {
      border-top-left-radius: @rxButton-button-group-border-radius;
      border-bottom-left-radius: @rxButton-button-group-border-radius;
    }

    &:last-of-type {
      border-top-right-radius: @rxButton-button-group-border-radius;
      border-bottom-right-radius: @rxButton-button-group-border-radius;
    }

    &:not(:last-of-type) {
      border-right: @rxButton-button-group-border;
    }

    &:hover,
    &:focus {
      cursor: pointer;
      color: @white;
      background: @rxButton-cancel-hover-background-color;
    }
  }//label

  input:checked + label {
    color: @white;
    background: @gray-700;
  }
}//.button-group

/*
 * rxCheckbox
 */
.rxCheckbox {
  display: inline-block;
  width: @rxCheckbox-size;
  height: @rxCheckbox-size;
  color: @rxCheckbox-color;
  background-color: @rxCheckbox-background-color;
  position: relative;

  input[type="checkbox"],
  .fake-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
  }

  .fake-checkbox {
    box-sizing: border-box;
    z-index: 0;
    width: 100%;
    height: 100%;

    // enabled unchecked
    background-color: @rxCheckbox-background-color;
    border: 1px solid @rxCheckbox-border-color;

    // perfect center alignment for tick
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    color: transparent;

    .tick {
      flex: 0 0 auto;
      font-size: @rxCheckbox-font-size;
    }
  }//.fake-checkbox

  &.rx-disabled + label {
    color: @app-disabled-text-color;
  }

  input[type="checkbox"] {
    z-index: 1;
    height: 110%;
    width: 110%;
    opacity: 0;
    cursor: pointer;

    // enabled checked
    &:checked + .fake-checkbox {
      background-color: @rxCheckbox-background-color-selected;
      color: @rxCheckbox-color;
      border: 0;
    }

    .disabled() {
      cursor: not-allowed;

      & ~ .fake-checkbox {
        background: @rxForm-input-background-color;
        border: 1px solid @rxCheckbox-border-color;
      }

      // disabled, checked
      &:checked ~ .fake-checkbox {
        background-color: @app-disabled-background-color;
        border: 0;
      }
    }//.disabled()

    &.ng-invalid.ng-dirty {
      // enabled unchecked error
      & + .fake-checkbox {
        background-color: @rxCheckbox-background-color;
        border: 2px solid @rxForm-error-accent-color;
      }

      // enabled checked error
      &:checked {
        & + .fake-checkbox {
          background-color: @rxForm-error-accent-color;
        }
      }

      &[disabled] {
        .disabled();
      }
    }

    &[disabled] {
      .disabled();
    }
  }
}//.rxCheckbox

