 * rxAccountInfo
 */
.rx-account-info {
  .info-panel {
    overflow: hidden;
  }
  .account-info-wrapper {
    float: left;
    margin-right: 5em;
  }
  .account-info-label {
    color: @gray-700;
  }
  .account-info-data {
    color: @gray-700;
    font-size: 26px;
    img {
      height: 35px;
      margin-right: 5px;
    }
  }
}

