 * rxAccountInfoBanner
 */
.rx-page {
  .account-info-banner {
    clear: both;
    background: @white;
    width: 100%;
    border-top: 1px solid;
    border-color: @gray-500;
  }

  ul.account-info-text {
    padding: 5px 50px 5px 12px;
    overflow: hidden;
    li {
      float: left;
      padding-right: 40px;
      .label {
        color: @gray-700;
        font-size: 9px;
      }
      .account-data {
        font-size: 18px;
        font-weight: 300;

