          width: 140px;
          font-size: @rxForm-font-size;
        }
      }
      .msg-warn {
        color: @red-700;
      }
      .msg-info {
        color: @orange-700;
      }
    }
    li.badges {
      float: right;
      padding: 0;
      .account-info-badge {
        .tooltip {
          width: 150px;
        }
        img {
          width: 35px;
          float: left;
          margin: 5px 2px;
        }
      }
    }
  }

  ul.account-info-badges {
    overflow: hidden;
  }
}

