/*
 * Common
 */
@import url("https://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300italic,400italic,500,500italic,700,700italic");
@import url("https://fonts.googleapis.com/css?family=Roboto+Mono:400,100,100italic,300,300italic,400italic,700,700italic");
@import "vars";
@import (inline) "normalize.css";

// Resets for HTML elements
html,
body {
  height: 100%;
  font-size: @app-font-size;
  color: @gray-900;
  font-family: @app-font;
}

// Resets for form inputs
input,
textarea,
select {
  box-shadow: none;
}

// Set default font
button,
input,
select,
.pure-g [class *= "pure-u"],
.pure-g-r [class *= "pure-u"] {
  font-family: @app-font;
}

// Remove default margin/padding/etc
header,
nav,
ol,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
fieldset {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

// No list styles for lists
ol,
ul,
li {
  list-style: none;
}

// We want a little bit more line-spacing in paragraphs
p {
  line-height: @app-line-height;
}

// Link Styles
a {
  text-decoration: none;
  color: @app-link-text-color;
}
a:hover,
a:focus {
  text-decoration: underline;
  color: @app-link-hover-text-color;
}

// Table Styles
table {
  clear: both;
  width: 100%;
  padding: 0;
  margin: 0;
  border-collapse: collapse;
  td,
  th {
    border: 1px solid @app-table-border-color;
    padding: @app-table-cell-padding;
    color: @app-table-cell-text-color;
  }
  thead th {
    text-align: left;
    font-weight: 500;
    background: @app-table-header-background-color;
    color: @app-table-header-text-color;
    border: 1px solid @app-table-header-border-color;
    &.actions {
      width: 15px;
    }
  }
  tr {
    background: @app-default-row-background-color;
  }
  &.table-striped {
    tr:nth-child(even),
    tbody:nth-child(odd) > tr {
      background: @app-striped-row-background-color;
    }
  }
  td.double-chevron-cell {
    width: 10px;
    padding: 0 10px;
    .double-chevron {
      cursor: pointer;
      &::after {
        display: inline-block;
        color: @gray-700;
        font-size: 20px;
        .chevron-mixin;
      }
    }
  }
  td.expanded {
    background-color: @app-table-expanded-chevron-background-color;
    border-bottom-color: @app-table-expanded-chevron-border-color;
    .double-chevron::after {
      color: @white;
      .chevron-mixin(-1);
    }
  }
  .fa-times.delete-x {
    color: @gray-700;
    cursor: pointer;
  }
  .fa-times.delete-x:hover {
    color: saturate(@red-700, 15%);
    cursor: pointer;
  }
}

.expanded-container {
  padding: 0 3px 20px 3px;
  background-color: @app-table-header-background-color;

  & > * {
    margin-left: 2%;
    width: 98%;
  }

  table.subtable {
    table-layout: fixed;

    th:first-child {
      border-left-width: 0;
    }

    th:last-child {
      border-right-width: 0;
    }

    th {
      border-color: @app-table-header-border-color;
      background-color: @app-table-header-background-color;
      color: @app-table-header-text-color;
      border-top: 0;
      border-bottom: 0;
    }
  }
  .expanded-content {
    background-color: @white;
    margin-top: 3px;
    .metadata {
      padding: 5px 10px;
    }
  }
}

/* ===== HEADINGS ===== */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1em 0;

  &.subdued {
    color: @gray-700;
  }
}

h1 {
  // same as .title.xl
  font-size: 28px;
}
h2 {
  // same as .title.lg
  font-size: 22px;
}
h3 {
  // same as .title
  font-size: 18px;
}
h4 {
  // same as .title.sm
  font-size: 16px;
}
h5 {
  // same as .title.xs
  font-size: 13px;
}
h6 {
  // no legacy equivalent
  font-size: 10px;
}

// Heading/Title Styles
.title {
  margin: 1em 0;

  // Sizing
  &.xl { // h1
    font-size: 28px;
  }
  &.lg { // h2
    font-size: 22px;
  }
  & { // h3
    font-size: 18px;
  }
  &.sm { // h4
    font-size: 16px;
  }
  &.xs { // h5
    font-size: 13px;
  }

  // Colors
  &.subdued {
    color: @gray-700;
  }
}//.title

// Statuses
.link-action,
.link-action a,
.link-warn,
.link-warn a {
  font-size: 1.2em;
  cursor: pointer;
}

.link-action:before,
.link-action a:before {
  content: "+ ";
}

.link-warn:before,
.link-warn a:before {
  content: "- ";
}

.action-separator {
  font-size: 1.1em;
  color: @subduedText;
}

.msg-action,
.msg-action a {
  color: @green-700;
  &:hover,
  &:focus {
    color: saturate(@green-700, 15%);
  }
}

.msg-warn,
.msg-warn a {
  color: @red-700;
  &:hover,
  &:focus {
    color: saturate(@red-700, 15%);
  }
}

.msg-info,
.msg-info a {
  color: @orange-700;
  &:hover,
  &:focus {
    color: saturate(@orange-700, 15%);
  }
}

.msg-info-blue,
.msg-info-blue a {
  color: @blue-700;
  &:hover,
  &:focus {
    color: saturate(@blue-700, 15%);
  }
}

// Lists
.list {
  li,
  .item {
    display: list-item;
    margin: 5px 0 0 30px;
  }

  ul&,
  &.bulleted {
    li,
    .item {
      list-style: disc;
    }
  }

  ol&,
  &.decimal {
    li,
    .item {
      list-style: decimal;
    }
  }
}

// Wells
.well {
  background: @gray-100;
  padding: 40px 50px;
  box-shadow: 0 0 0 10px white inset;

  p {
    line-height: 1.5em;
    color: @gray-700;
  }

  ul {
    display: inline-block;
    text-align: left;
  }

  h2 {
    color: @gray-700;
  }
}

.modal-well {
  background-color: @gray-100;
  padding: 20px 30px;

  h2 {
    font-size: 14px;
  }
}

// Other Stuff
.subdued {
  color: @gray-700;
}

// Add hover style if a link or inside a link
a.subdued:hover,
a.subdued:focus,
a:hover .subdued,
a:focus .subdued {
  color: @subduedTextHover;
}

// Hidden content
.hidden {
  visibility: hidden;
}

// Used to hide content visually but let them still be read
// by screen readers (removed from the page spacing)
// !important is required and prevents visible styling
.visually-hidden {
  position: absolute !important; // lesshint importantRule: false
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important; // lesshint importantRule: false
  border: 0 !important; // lesshint importantRule: false
  height: 1px !important; // lesshint importantRule: false
  width: 1px !important; // lesshint importantRule: false
  overflow: hidden;
}
body:hover .visually-hidden a,
body:hover .visually-hidden input,
body:hover .visually-hidden button {
  display: none;
}

// Clearing Floats
.clearfix:before,
.clearfix:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.clearfix:after {
  clear: both;
}

// Clearing siblings
.clear {
  clear: both;
}
.clear-left {
  clear: left;
}
.clear-right {
  clear: right;
}

// Marks content as full width (e.g. to give a button full width).
// Requires 'display' to be defined as `inline-block` or `block`
.full-width {
  width: 100%;
}

// Center content
// Requires 'display' to be defined as `inline-block` or `block`
.centered {
  text-align: center;
}


/*
 * PLACEHOLDER TEXT STYLING
 * https://css-tricks.com/snippets/css/style-placeholder-text/
 */
::-webkit-input-placeholder { // Chrome
  color: @inputPlaceholderColor;
  font-weight: 300;
  font-style: italic;
}
::-moz-placeholder { // Firefox 19+
  color: @inputPlaceholderColor;
  font-weight: 400;
  font-style: italic;
}

//DEPRECATED: A "filter" style rxSearchBox
.filter-box {
  font-style: italic;
}

@import "vars";
@import "mixins";

.account-users {
  box-sizing: border-box;
}

.rx-app {
  @navTransitionDuration: 0.2s;
  @appMenuWidth: 300px;

  min-height: 100%;
  display: flex;
  flex-direction: row;
  background: @gray-50;

  .rx-app-content {
    flex: 1 1 0;
  }

  .rx-app-menu {
    flex: 0 0 @appMenuWidth;
    color: @white;
    font-size: 15px;
    font-weight: 300;
    background: @gray-975;
    line-height: @app-line-height;
    padding-bottom: 10em;
    position: relative;

    .site-title,
    .nav-section-title {
      margin: 0;
      font-size: inherit;
    }
  }

  .site-branding {
    border-top: 1px solid @blue-700;
    border-bottom: 1px solid @blue-700;
    padding: 5px 20px;
    overflow: hidden;
    background: @blue-900;
    position: relative;

    .site-title {
      float: left;
    }
    .site-options {
      float: right;
      font-size: 0.8em;
      margin-top: 1px;

      .site-option {
        color: @blue-100;
        display: inline-block;
        margin-left: 1em;
        &:hover,
        &:focus {
          color: @white;
        }
      }
    }

    .collapsible-toggle {
      position: absolute;
      text-align: center;
      top: 0;
      bottom: 0;
      right: 0;
      width: 2em;
      border-left: 1px solid darken(@blue-900, 10%);
      &:hover {
        background: lighten(@blue-900, 10%);
      }
    }

    .double-chevron {
      font-size: 20px;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;

      &::before {
        color: @blue-100;
        content: "\00bb";
      }

      &.double-chevron-left::before {
        content: "\00ab";
      }

      &:hover &::before {
        color: @white;
      }
    }
  }

  &.collapsible {
    .site-branding {
      padding-right: calc(~"2em + 20px");
    }
  }

  &.collapsed {
    .rx-app-menu {
      flex: 0 0 25px;
      padding-bottom: 0;
      position: relative;
    }

    .site-branding,
    .collapsible-toggle {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      padding: 0;
      width: 100%;
    }

    .site-title,
    .site-options,
    .rx-app-nav,
    .rx-app-help {
      display: none;
    }

    .page-body {
      & > .rx-notifications {
        left: 25px;
      }
    }
  }

  &.warning-bar {
    position: absolute;
    margin-top: 3em;
    width: 100%;
  }

  &.warning-bar.preprod {
    .rx-app-menu {
      background: @preprod-background-color;
    }
    .rx-app-nav-item {
      .item-link {
        background: @preprod-background-color;
        border-top: 1px solid @preprod-border-top-color;
        border-bottom: 1px solid @preprod-border-bottom-color;
        &:hover,
        &:focus {
          background: @preprod-focus-background-color;
          border-top: 1px solid @preprod-focus-border-top-color;
          border-bottom: 1px solid @preprod-focus-border-bottom-color;
        }
      }

      // active nav, no children (orange/yellow bg, arrow right)
      &.active:not(.has-children) > .item-link {
        border-color: transparent;
        background: @preprod-active-item-background-color;
        color: @preprod-active-item-text-color;
        &:after {
          right: -6px;
          border-left-color: @preprod-active-item-arrow-color;
        }
      }

      // active nav, w/children (double arrow up, gray bg)
      &.active.has-children > .item-link {
        border-top-color: @preprod-border-top-color;
        // used to show BG of arrow (because :after is rotated)
        &:before {
          background: @preprod-active-menu-background-color;
        }
        .item-content {
          border-bottom: 1px solid @black;
        }
      }

      .item-content {
        background: @preprod-active-menu-background-color;
      }
    }
  }

  .rx-app-nav {
    clear: both;

    .nav-section {
      padding-left: 5px;
    }

    .nav-section-title {
      padding: 5px 15px;
      font-size: 13px;
    }

    .nav-section-all {
      background: @green-700;
      border-top: 1px solid @green-500;
    }

    .nav-section-highlight {
      background: @blue-500;
      border-top: 1px solid lighten(@blue-500, 10%);
    }

    .nav-section-no-title {
      background: transparent;
      padding: 0;
      margin: 0;

      & > .nav-section-title {
        display: none;
      }
    }
  }

  .rx-app-nav-item {
    clear: both;

    // common nav styles (inactive nav, no children)
    .item-link {
      background: @gray-975;
      border-top: 1px solid @gray-950;
      border-bottom: 1px solid @black;
      padding: 10px 4em 10px 15px;
      display: block;
      color: @white;
      text-decoration: none;
      -webkit-transition: background @navTransitionDuration;
      transition: background @navTransitionDuration;
      position: relative;
      &:hover,
      &:focus {
        background: @gray-950;
        border-top: 1px solid @gray-900;
        border-bottom: 1px solid @black;
      }
      &:before,
      &:after {
        content: "";
        position: absolute;
      }
      &:after {
        -webkit-transition: all @navTransitionDuration;
        transition: all @navTransitionDuration;
      }
    }

    &:not(.has-children) > .item-link:after {
      right: 0;
      top: 0;
      border: transparent solid;
      border-width: calc(~"5px + 1em") 6px;
      border-right: 0;
    }

    // active nav, no children (blue bg, arrow right)
    &.active:not(.has-children) > .item-link {
      border-color: transparent;
      background: @blue-700;
      &:after {
        right: -6px;
        border-left-color: @blue-700;
      }
    }

    // inactive nav, w/children (double arrow down)
    &.has-children > .item-link:after {
      font-size: 2em;
      content: "\00bb"; // angle quotation mark, right (&raquo;)
      -webkit-transform: rotate(90deg) scaleX(1);
      transform: rotate(90deg) scaleX(1);
      top: 50%;
      line-height: 0;
      right: 0.45em;
    }

    // active nav, w/children (double arrow up, gray bg)
    &.active.has-children > .item-link {
      border-top-color: @gray-900;
      border-bottom-width: 0;
      // used to show BG of arrow (because :after is rotated)
      &:before {
        width: 3em;
        right: 0;
        top: 0;
        bottom: 0;
        background: @gray-950;
      }
      &:after {
        // flip arrow around
        -webkit-transform: rotate(90deg) scaleX(-1);
        transform: rotate(90deg) scaleX(-1);
      }
      .item-content {
        border-bottom: 1px solid @black;
      }
    }

    .item-content {
      background: @gray-950;
      padding: 10px;
    }

    .item-directive:not(.ng-hide) + .item-children {
      margin-top: 10px;
    }

    .item-children {
      padding: 10px;
      background: @gray-900;
    }

    .child-header {
      margin-bottom: 1em;
    }

    .current-search {
      text-transform: uppercase;
      font-weight: bold;
      color: @gray-600;
      font-size: 10px;
      display: block;
    }

    .current-result {
      color: @gray-400;
      font-size: 18px;
    }

    // Legacy select elements in navigation
    // TODO: Deprecated
    .field-select {
      select {
        background: #404140;
        border: 1px solid #3a3a3a;
      }
      &:before {
        background: #404140;
      }
      &:after {
        right: 10px;
        top: 5px;
      }
    }

    // User select element in Account section of nav
    // TODO: Update to 2.0 Color Variables
    .account-users {
      float: right;
      width: 50%;

      .rxSelect {
        background: #404140;
        color: #d5d5d3; // same as .current-result above

        .fake-select {
          border-color: #3a3a3a;

          .select-trigger {
            background: #404140; // same as .rxSelect bg
          }
        }
      }
    }
  }

  .rx-app-nav-level-2 {
    .item-link {
      background: @gray-800;
      border-top-color: @gray-700;
      border-bottom-color: @gray-900;
      font-size: 13px;

      &:hover,
      &:focus {
        background: @gray-900;
        border-top: 1px solid @gray-700;
        border-bottom: 1px solid @gray-900;
      }
    }
    .active:not(.has-children) > .item-link {
      margin-right: -10px;
    }
    .active.has-children > .item-link {
      background: @gray-975;
      border-color: transparent;
    }

    .item-content {
      background: @gray-975;
      padding: 0 0 4px 4px;
    }

    .item-children {
      padding: 0;
    }
  }

  .rx-app-nav-level-3 .item-link {
    background: @gray-950;
    border-color: transparent;
    padding: 5px 15px;

    &:hover,
    &:focus {
      background: @gray-900;
      border-color: transparent;
    }
  }
  .rx-app-nav-level-3 .rx-app-nav-item {
    &:first-child {
      border-top: 3px solid @gray-950;
    }
    .item-link:after {
      border-top-width: 1em;
      border-bottom-width: 1em;
    }
  }

  .rx-app-search {
    position: relative;
    font-size: 14px;
    color: @gray-500;

    fieldset {
      position: relative;
    }

    // NOTE: We have to have this duplication because CSS doesn't like combining the placholder selectors
    .placeholder() {
      font-style: italic;
      text-overflow: ellipsis;
    }

    ::-webkit-input-placeholder {
      .placeholder();
    }
    ::-moz-placeholder {
      .placeholder();
    }
    :-ms-input-placeholder {
      .placeholder();
    }

    .search-input {
      .box-sizing(border-box);
      background: @gray-900;
      border: 0;
      width: 100%;
      padding: 9px;
      padding-right: 30px;
      margin: 0;
    }

    .search-input.ng-invalid:focus {
      outline-color: @red-700;
    }

    .search-action {
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 100%;
      background: url("images/magnifying-glass.png") no-repeat 50% 50%;
      border: 0;
      opacity: 0.2;
    }

    .search-option {
      margin-top: 10px;
    }
  }

  .rx-app-help {
    position: fixed;
    bottom: 0;
    left: 0;
    width: @appMenuWidth;
    background: @gray-950;
    border-top: 1px solid @gray-900;
    line-height: 3em;
    padding-bottom: 1px;
    font-size: 0.8em;
    text-shadow: 1px 1px 0px rgba(26, 26, 26, 0.2);

    .rx-feedback .modal-link {
      background: url("images/icon-feedback.png") no-repeat 20px calc(~"50% + 1px");
      vertical-align: middle;
      color: @orange-500;
      padding: 0 21px 0 41px;
      float: right;
    }
  }

  // Page Styles
  // TODO: Update to 2.0 Color Variables
  .page-header {
    background: linear-gradient(to bottom, #e7e7e7 0%, #d6d6d6 100%);
    border-bottom: 1px solid #bbbbbb;
  }

  .page-body {
    padding: 20px 30px 50px;
    clear: both;

    .page-titles {
      float: left;

      .page-title {
        margin: 0;
        position: relative;
        word-break: break-all;
      }

      .page-subtitle {
        margin-top: 0;
        word-break: break-all;
      }
    }

    & > .rx-notifications {
      position: fixed;
      z-index: 20;  // accommodate rxSelect .fake-select class
      top: 0;
      left: 300px;
      right: 100px;
      opacity: 0.95;
      .rx-notification {
        width: 100%;
      }
    }

    .page-actions {
      float: right;
      margin: 0 0 2em 2em;
    }
  }
}

.warning-bar.rx-notifications {
  position: fixed;
  z-index: 3;
  width: 100%;
  margin-bottom: 0;
}

.status-tag {
  &.alpha-status,
  &.beta-status {
    margin-left: 10px;
    text-transform: uppercase;
    font-size: 9px;
    border: 1px solid;
    padding: 1px 2px;
    .border-radius(2px);
    top: 9px;
    color: @blue-900;
  }
}

/* Disabled link styles */
.link-disabled {
  pointer-events: none;
  a {
    color: @gray-600;
  }
  .rx-spinner {
    margin-left: 10px;
    border-bottom-color: @gray-600;
    border-right-color: @gray-600;
  }
}

a.disabled {
  color: #d5d3d3;
  cursor: default;
  pointer-events: none;
  text-decoration: none;
}

@import "vars";

.rx-breadcrumbs {
  overflow: hidden;
  border-style: solid;
  border-color: @gray-100;
  float: left;

  .breadcrumb {
    float: left;

    .breadcrumb-name {
      padding: @rxBreadcrumbs-height/2 1em @rxBreadcrumbs-height/2 2em;
      font-size: 0.9em;
      display: block;
      color: @gray-900;
      position: relative;
      text-decoration: none;
      text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
      background: @gray-400;
    }
    .breadcrumb-name.last {
      background: transparent;
    }

    a::after,
    a::before {
      content: "";
      position: absolute;
      top: 50%;
      margin-top: -@rxBreadcrumbs-height;
      border: 0 solid transparent;
      border-width: @rxBreadcrumbs-height 0 @rxBreadcrumbs-height @rxBreadcrumbs-arrow-width;
      right: -@rxBreadcrumbs-arrow-width;
    }

    a::after {
      /* TODO add in border gradient to match nav background */
      border-left-color: @gray-400;
      z-index: 2;
    }
    a::before {
      border-left-color: @gray-500;
      right: -(@rxBreadcrumbs-arrow-width + 1);
      z-index: 1;
    }

    .first {
      padding-left: 1em;
      background: @gray-500;
    }
    .first::after {
      border-left-color: @gray-500;
    }

    a:hover {
      background: @white;
    }
    a:hover::after {
      border-left-color: @white;
    }
  }

  .status-tag {
    &.alpha-status,
    &.beta-status {
      margin-left: 6px;
      position: static;
    }
  }

}

.rxBulkSelect-demo {
  .pending-status {
    color: @orange;
  }
  .success-status {
    color: @green;
  }
  .failure-status {
    color: @red;
  }
}

/*
 * rxBulkSelect
 */

thead th.bulk-select-header {
  background: @app-selected-row-background-color;
  text-align: center;
  color: @rxBulkSelect-header-text-color;
  font-weight: normal;
  border: 1px solid @app-table-border-color;
}

thead th[rx-bulk-select-header-check] {
  width: @rxCheckbox-size;
}

.header-button.btn-link {
  color: @app-link-text-color;
  &:focus {
    color: @app-link-text-color;
  }
  &:hover {
    color: @app-link-hover-text-color;
  }
}

.batch-action-menu-container {
  position: relative;
  width: 5px;
  height: 0;
  & > i {
    cursor: pointer;
    color: @gray-700;
    &:hover {
      color: @gray-800;
    }
  }
}

.batch-actions-area {
  font-size: 115%;
  line-height: 2em;
}

.batch-action-list-hideable {
  float: left;
  z-index: 99;
  position: absolute;
  top: 0;
  right: -115px;
  min-width: 20em;
  font-size: 12px;
  border: 4px solid @white;
  border-radius: 4px;
  background: @white;
  text-shadow: rgba(0, 0, 0, 0.09) 1px 1px 1px;
  box-shadow: rgba(0, 0, 0, 0.35) 1px 1px 4px 0px;
  padding: 6px;

  &:before {
    content: "\f0d8";
    text-shadow: rgba(0, 0, 0, 0.35) 0 -1px 1px;
    font-family: FontAwesome;
    text-decoration: inherit;
    color: @white;
    font-size: 24px;
    display: block;
    left: 230px;
    position: absolute;
    top: -23px;
    width: 0;
    z-index: 1;
  }

  i {
    margin-right: 5px;
  }
}

.batch-action-list {
  float: right;
  z-index: 5;
}

.selected td:not(.status) {
  background: @app-selected-row-background-color;
  border-color: @green-100;
  // this 'double' makes it so that the border-color applies to the top border as well
  // @see http://stackoverflow.com/questions/7942212/css-table-row-border-color-with-border-collapse
  border-style: double;
}

.batch-action-modal-table .pagination {
  & > li:nth-child(1) {
    display: none;
  }
  & > li:nth-child(2) {
    display: none;
  }
  .pagination-per-page {
    display: none;
  }
}

/*
 * rxCharacterCount
 */

// Wrapper provides proper width for text area and positioning
// character count. For specific use cases in a project,
// custom wrappers with custom widths can be set.
.counted-input-wrapper {
  width: 375px;
  position: relative;
  background-color: @rxForm-input-background-color;

  // These two selectors must always be styled with the same width.
  .input-highlighting,
  textarea {
    width: 370px;
  }

  input,
  textarea {
    // Ensure the input is layered over the highlighting without z-index
    position: relative;
    background-color: transparent;
    resize: none;
    .inputBorderPadding;
  }

  .input-highlighting {
    position: absolute;
    top: 4px;
    left: 6px;
    white-space: pre-wrap;
    word-wrap: break-word;
    color: transparent;

    .over-limit-text {
      background-color: @rxForm-error-text-color;
      opacity: 0.3;
    }
  }

  .character-countdown {
    text-align: right;
    color: @app-label-text-color;

    &.near-limit {
      color: @orange-700;
    }
    &.over-limit {
      color: @rxForm-error-text-color;
    }
  }
}

/*
 * rxCollapse
 */
rx-collapse {
  display: block;

  .collapse-container {

    .double-chevron-cell {
      display: inline-block;
      .box-sizing(border-box);
      width: 30px;
      padding: 0 10px;
      background-color: @gray-500;
      vertical-align: middle;
      cursor: pointer;

      .double-chevron {
        &:after {
          display: inline-block;
          color: @white;
          font-size: 28px;
          .chevron-mixin;
        }
      }

      &.expanded {
        .double-chevron {
          &:after {
            .chevron-mixin(-1);
          }
        }
      }
    }

    border: 2px solid @gray-500;

    .rx-collapse-title {
      display: inline-block;
      margin: 0 10px;
      vertical-align: middle;
    }

    .collapse-body {
      margin: 0;
      padding: 10px;
    }
  }
  .sml-title {
    cursor: pointer;
    color: @gray-700;
    font-size: 10pt;
  }

  .hide-border {
    border: 0;
  }
}

/*
 * rxFeedback
 */
.rx-feedback-form .feedback-description {
  .field-label,
  .field-input,
  .feedback-textarea {
    display: block;
    width: 100%;
    float: none;
  }

  .field-input {
    top: 0;
    margin-top: 5px;
  }

  .feedback-textarea {
    height: 10em;
  }
}

/*
 * rxFloatingHeader
 */
.rx-floating-header {
  position: fixed;
  z-index: 3;
  opacity: 0.96;
}


// For tables with filters, style filter th differently
th.filter-header {
  font-weight: normal;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid @gray-300;
  input.filter-box {
    color: @gray-900;
  }
  button {
    margin: 0;
  }
  [rx-form] {
    margin: 10px 0;
    rx-form-section {
      margin: 0;
      rx-field {
        margin-bottom: 0;
      }
    }
  }
}

// Batch actions menu is visible even when header floats
// There's some black magic here. For whatever reason, we have to
// add z-index:2 to the other rows in the header, *not* the row
// that the batch actions menu lives in.
tr.rx-floating-header:not(.rx-table-filter-row) {
  z-index: 2;
}

// Added to accommodate dropdown boxes to display "on top" of rx-floating-header
// components
thead .rx-floating-header:first-child {
  z-index: 3;
}

