    margin-top: 5px;
  }
}

/* Dropdowns */
// deprecated
.field-select {
  display: inline-block;
  position: relative;
  width: @inputSelectWidth;

  select {
    width: 100%;
    margin: 0;
    .border-radius(0);
    background: @white;
    border: 0;
    outline: none;
    display: inline-block;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    .inputBorderPadding;
  }

  &:before {
    // background so long text doesn't show behind caret
    content: "";
    right: 1px;
    top: 1px;
    width: 20px;
    height: 93%;
    background: @white;
    position: absolute;
    pointer-events: none;
    display: block;
  }

  &:after {
    // down caret from font awesome
    content: "\f0d7";
    font-family: FontAwesome;
    font-size: 15px;
    color: #aaaaaa;
    right: 10px;
    top: 7px;
    padding: 0;
    position: absolute;
    pointer-events: none;
  }
}

/* Submit buttons */

.form-actions {
