/* Compressed Layout */
// DEPRECATED - No longer valid
.rx-form.layout-compressed {
  .rx-form-row {
    display: flex;
    flex-wrap: nowrap;
    flex: 1 auto;

    .form-item {
      padding: 0 3px;
      flex: 1;
    }
  }

  .form-item {
    display: block;

    .field-input {
      width: 100%;
      display: block;

      select,
      textarea {
        width: 100%;
      }

      input {
        &:not([type="checkbox"]),
        &:not([type="radio"]) {
          width: 100%;
        }

        &[type="checkbox"],
        &[type="radio"] {
          width: 1em;
        }
      }
    }

    .field-label,
    .field-legend {
      font-size: 0.8em;
      display: block;
    }
  }

  // This is selecting based off of the element so that we can be sure to set
  // this if there is a prefix OR a suffix set. We can't assume they'll both
  // always be there.
  rx-form-item[suffix],
  rx-form-item[prefix] {
    .field-input {
      display: inline-block;
      width: auto;
    }
  }
}


/* NEW rxForm "2.0" STYLES  */
input,
textarea,
select {
  box-sizing: border-box;
  border: 0;
  outline: none;
}

[rx-form],
rx-form-section,
rx-field,
rx-field-name,
rx-field-content,
rx-input,
rx-inline-error,
rx-help-text {
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0;
}

rx-inline-error {
  color: @rxForm-error-text-color;
  font-weight: 900;
}

rx-help-text {
  font-size: 0.8em;
  font-style: italic;
  margin: 1em 0em;
}

rx-field-name {
  color: @app-label-text-color;
  font-size: @app-label-font-size;

  .wrapper {
    box-sizing: border-box;
    margin: 0;
    padding: 0;

    .required-symbol {
      font-weight: 900;
      color: @app-required-symbol-color;
    }
  }
}//rx-field-name

[rx-form] {
  font-size: @rxForm-font-size;
  margin: @rxForm-gutter 0;

  rx-field-name,
  rx-help-text,
  rx-inline-error,
  rx-input {
    margin-bottom: (@rxForm-gutter / 2);

    &:last-child {
      margin-bottom: 0;
    }
  }

  rx-form-section {
    // as flex container
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;

    margin: 0 (@rxForm-gutter / 2);

    & > div,
    rx-field {
      margin: 0 (@rxForm-gutter / 2) (@rxForm-gutter * 1.25);

      // http://www.w3.org/TR/css3-flexbox/images/rel-vs-abs-flex.svg
      // as dynamic flex item (3rd arg acts like min-width)
      flex: 1 0 @rxForm-field-min-width;
    }

    rx-field {
      // as flex container
      display: flex;
      flex-flow: column nowrap;
      justify-content: flex-start;

      rx-field-content {
        flex: 1 0 auto; // as flex item
        margin-bottom: @rxForm-gutter;

        &:last-child {
          margin-bottom: 0;
        }

        .validInput() {
          padding: 3px 5px;
          border: 1px solid @rxForm-input-border-color;
        }//.validInput()

        .invalidInput() {
          border: 2px solid @rxForm-input-border-color-invalid;
          // MUST differ with valid padding by 1px to
          // account for thicker border
          padding: 2px 4px;
        }//.invalidInput()

        .disabledInput() {
          border: 1px solid @rxForm-input-border-color;
          color: @app-disabled-text-color;
          background: @app-disabled-background-color;
          cursor: not-allowed !important; // lesshint importantRule: false
        }//.disabledInput()

        rx-input {
          // as a flex container
          display: flex;
          flex-flow: row nowrap;
          justify-content: flex-start;
          align-items: stretch;

         .button {
            margin-bottom: 0;
          }

          & > input,
          & > textarea {
            flex: 1 1 auto; // as dynamic flex item

            min-height: @rxForm-input-min-height;

            &,
            &.ng-valid {
              .validInput();
            }

            &.ng-invalid.ng-dirty {
              .invalidInput();

              // DISABLED STYLING
              // (these should trump all other styles)
              &[disabled] {
                .disabledInput();
              }
            }

            // DISABLED STYLING
            // (these should trump all other styles)
            &[disabled] {
              .disabledInput();
            }
          }

          rx-prefix,
          rx-suffix,
          rx-infix {
            box-sizing: border-box;
            min-height: @rxForm-input-min-height;
            padding: 0 (@rxForm-gutter / 4);
            flex: 0 0 auto; // as static flex item

            // as flex container
            display: flex;
            align-items: center; // content is vertically centered
          }

          rx-prefix:first-child {
            padding-left: 0;
          }
          rx-suffix:last-child {
            padding-right: 0;
          }

          /* ===== Other Directive Support ===== */
          .rxSelect {
            flex: 1 1 auto; // as dynamic flex item
          }

          rx-multi-select {
            flex: 1 1 auto; // as dynamic flex item
          }

          rx-date-picker {
            flex: 0 0 auto; // as static flex item
          }

          .rxCheckbox,
          .rxRadio,
          rx-toggle-switch {
            flex: 0 0 auto; // as static flex item

            & + label {
              margin-left: 5px;
            }
          }

          .rxCheckbox,
          .rxRadio,
          rx-toggle-switch {
            &[disabled] + label {
              color: @app-disabled-text-color;
            }
          }

          .rx-option-table {
            table-layout: fixed;
            width: 100%;
          }

          // rxCharacterCount support
          .counted-input-wrapper {
            flex: 1 0;

            input,
            textarea {
              width: 100%;

              .validInput();

              &.ng-invalid.ng-dirty {
                .invalidInput();

                // DISABLED STYLING
                // (these should trump all other styles)
                &[disabled] {
                  .disabledInput();
                }
              }

              // DISABLED STYLING
              // (these should trump all other styles)
              &[disabled] {
                .disabledInput();
              }
            }
          }

          rx-search-box {
            flex: 1 1;
          }
        }//rx-input
      }//rx-field-content
    }//rx-field

    // replaces .form-actions
    // [rx-form] > rx-form-section > div
    & > div {
      margin-top: (@rxForm-gutter / 2);

      .button {
        margin: 2px;

        &:first-of-type {
          margin-left: 0;
        }
        &:last-of-type {
          margin-right: 0;
        }
      }
    }

    &[stacked] {
      flex-flow: column nowrap;

      & > div,
      rx-field {
        flex: 1 0; // grows vertically, does not shrink
        max-width: @rxForm-field-max-width;
        margin-bottom: (@rxForm-gutter * 1.25);
      }
    }

    &[controlled-width] {
      & > div,
      rx-field {
        max-width: @rxForm-field-max-width;
      }
    }
  }//rx-form-section
}//[rx-form]

/*
 * rxInfoPanel
 */
.info-panel {
  clear: both;
  background: @white;
  border-color: @blue-700;
  border-width: 0 5px 5px 5px;
  border-style: solid;
  padding: 0 10px 10px 10px;
  overflow: hidden;

  .info-title {
    background: @blue-700;
    color: @white;
    width: 100%;
    padding: 8px 10px;
    margin: 0 0 10px -10px;
  }
}

/*
 * rxMetadata
 */
rx-metadata {
  box-sizing: border-box;
  padding: 0;
  margin: @rxMetadata-gutter 0;

  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: stretch;

  rx-meta {
    display: block;
    margin: 0 0 22px;

    .label {
      color: @rxMetadata-label-color;
      font-size: @rxMetadata-label-font-size;
      margin: 0 0 6px;
    }

    .definition {
      color: @rxMetadata-definition-color;
      font-size: @rxMetadata-definition-font-size;
      font-weight: 700;
    }

    &.force-word-break {
      word-break: break-all;
    }
  }

  section {
    box-sizing: border-box;
    display: block;
    padding: 0;
    margin: 0 @rxMetadata-gutter 0 0;
    flex: 0 0 300px; // no grow, no shrink, always 300px wide;

    &:last-child {
      margin-right: 0;
    }
  }
}

@import "mixins";
@import "vars";

/**
 * rxModalAction.less
 *
 * Stylesheet for modals, repurposed from the modal component of http://angular-ui.github.io/bootstrap
 *
 * @version 1.0
 * @author Ernie Hsiung
 */

 .modal-link {
     &[disabled],
     &[disabled]:hover {
         cursor: not-allowed;
         color: @gray-600;

         &.button {
             background-color: @gray-300;
         }
     }
 }

.modal,
.modal-backdrop {
  .transition();
  .opacity(0);
  &.fade.in {
    .opacity(1);
  }
}

.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  background: @black;
  &.fade.in {
    .opacity(0.4);
  }
}

.modal {
  position: absolute;
  top: 10%;
  left: 50%;
  z-index: 1050;
  width: 560px;
  background: @white;
  border: 1px solid rgba(0, 0, 0, 0.3);
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  -moz-transform: translate(-50%, 0);
  .box-shadow(0 3px 7px rgba(0, 0, 0, 0.3));
  .background-clip();
  outline: none;
}

.modal-header {
  padding: @rxModalAction-padding;
  border-bottom: 1px solid @gray-200;
  position: relative;

  .modal-title {
    line-height: 1;
    font-size: 1.75em;
    margin: 0;
  }
  .modal-subtitle {
    line-height: 1.5;
    font-size: 1em;
    font-wieght: bold;
    color: @gray-700;
    margin: 0;
  }
  .modal-close {
    position: absolute;
    top: @rxModalAction-padding;
    right: @rxModalAction-padding;
    font-size: 2em;
    width: 1em;
    height: 1em;
    text-align: center;
    color: @gray-600;
    &:hover,
    &:focus {
      color: @gray-800;
    }

    &:before {
      content: "\00D7";
      position: absolute;
      line-height: 0;
      right: 0;
      width: 1em;
    }
  }
}
.modal-body {
  position: relative;
  overflow-y: auto;
  max-height: 400px;
  padding: @rxModalAction-body-padding @rxModalAction-padding;

  .loading {
    margin: 0 auto;
    width: 1em;
  }

  .form-radio {
    border: 0;
  }

  .rx-form.modal-form {
    margin: 0;
  }
}

.modal-footer {
  padding: @rxModalAction-padding;
  border-top: 1px solid @gray-200;
}

@media (max-width:480px) {
  @padding: 10px;
  .modal {
    position: absolute;
    top: @padding;
    left: @padding;
    right: @padding;
  }
  .modal-header .close {
    padding: @padding;
    margin: -@padding;
  }
}

.rxMultiSelect-demo {
  .multi-select-container {
    width: 200px;
  }

  table {
    table-layout: fixed;
  }
}

/*
 * rxMultiSelect
 */
rx-multi-select {
  #vars.rxMultiSelect();
  z-index: auto;
  box-sizing: border-box;
  display: block;
  color: @container-list-text-color;
  background: @container-background-color;
  position: relative;

  // Element should be resizable via wrapper alone
  min-width: @container-min-width;

  border: 1px solid @container-border-color;

  .control {
    box-sizing: border-box;
    display: flex;
    flex-flow: row no-wrap;
    justify-content: space-around;
    align-items: stretch;
    height: @container-height;
    cursor: pointer;

    .preview {
      flex: 1 1;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin: 0 5px;
      overflow: hidden;
      white-space: nowrap;
    }

    .select-trigger {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 @trigger-width;
      color: @trigger-color;
    }
  }

  .menu {
    z-index: 100;
    position: absolute;
    cursor: pointer;
    top: -1px;
    left: -1px;
    width: 100%;
    border: 1px solid @container-border-color;
    background: @container-background-color;
    box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.35);
  }

  .disabled() {
    color: @container-list-disabled-text-color;
    cursor: not-allowed ;
    border-color: @container-disabled-border-color;
    border-width: @container-border-width;
    background: @container-disabled-background-color;

    .select-trigger {
      background: @trigger-disabled-background-color;
      border-left-color: @trigger-disabled-color;
      color: @trigger-disabled-color;
    }
  }//.disabled()

  &.ng-invalid.ng-dirty {
    border-color: @container-invalid-border-color;
    border-width: 2px;

    .select-trigger {
      color: @trigger-invalid-color;
    }

    &[disabled] {
      .disabled();
    }
  }//.ng-invalid.ng-dirty

  &[disabled] {
    .disabled();
  }

  rx-select-option[value="all"] label {
    border-bottom: 1px solid @container-border-color;
  }

  .backdrop {
    z-index: 90;
    position: fixed;
    top: 0;
    left: 0;
