.animate-fade {
  &.ng-enter,
  &.ng-leave {
    -webkit-transition: 0.25s cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
    -moz-transition: 0.25s cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
    -ms-transition: 0.25s cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
    -o-transition: 0.25s cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
    transition: 0.25s cubic-bezier(0.250, 0.250, 0.750, 0.750) all;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }

  &.ng-leave.ng-leave-active,
  &.ng-enter {
    opacity: 0;
  }

  &.ng-leave,
  &.ng-enter.ng-enter-active {
    opacity: 1;
  }
}//.animate-fade

/*
 * rxOptionTable
 */
.rx-option-table {
  // we want the table to not look ugly when stretched across the screen
  width: auto;
  // we also want a semi-consistent width, but not too wide for modals
  min-width: 39em;

  td:not(.empty-message) {
    // remove default padding from td's so that we can make the label take up the full room
    padding: 0;
  }
  .fillWrapper {
    box-sizing: border-box;
    display: inline-block;
    height: 100%;
    width: 100%;
    position: relative;

    label {
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      padding: @app-table-cell-padding;
      cursor: pointer;

      display: flex;
      // vertically centered content in label
      align-items: center;

      .alignWrapper {
        flex: 1 1 auto;
      }
    }

    rx-help-text {
      margin: 0;
    }
  }
  input[disabled] {
    cursor: not-allowed;
  }
  .disabled {
    label {
      cursor: default;
      color: @gray-600;
    }
  }
  .option-table-input {
    text-align: center;
    line-height: 0;
  }
  .current td {
    color: @green-700;
  }
  // @note KL: I'd like to stick with just adding the background to the <tr>, but
  // we need to make the selector more specific to override the table-stripe styles.
  .selected td {
    background: @app-selected-row-background-color;
    border-color: @green-100;
    // this 'double' makes it so that the border-color applies to the top border as well
    // @see http://stackoverflow.com/questions/7942212/css-table-row-border-color-with-border-collapse
    border-style: double;
  }

  thead {
    tr {
      th:first-child {
        width: @rxCheckbox-size;
      }
    }
  }
}

/*
 * rxPaginate
 */

.paginate-area {
  text-align: center;
  td {
    padding: 0;
    background: @white;
  }
}

.rx-paginate {
  .pagination {
    display: flex;
    flex-flow: row nowrap;
    padding-left: 0;
    width: 100%;
  }

  .pagination li {
    display: inline;
  }

  .pagination li a {
    cursor: pointer;
  }

  .pagination li a,
  .pagination li span {
    position: relative;
    float: left;
    padding: 6px 12px;
    margin-left: -1px;
    text-decoration: none;
    background: @white;
    border-top: 2px solid transparent;
    color: @rxPaginate-pagination-text-color;
  }

  .pagination > li:first-child {
    order: 1;
  }

  .pagination > li:first-child > a,
  .pagination > li:first-child > span {
    padding-left: 30px;
    color: @blue-700;
    background: transparent;
    &:before {
      content: ".";
      width: 0;
      height: 0;
      font-size: 0;
      display: block;
      position: absolute;
      border: 6px solid transparent;
      border-bottom-color: @blue-700;
      border-top: 0;
      left: 10px;
      top: 55%;
      margin-top: -4px;
    }
  }

  .pagination > li:nth-child(2) {
    order: 2;
    padding: 6px 12px;
    border-top: 2px solid transparent;
    color: @rxPaginate-pagination-text-color;
  }

  .pagination > .page-links {
    order: 3;
    margin: auto;
    align-self: center;
  }

  .pagination > li:last-child {
    order: 4;
    border-top: 2px solid transparent;
    padding: 6px 12px;
    color: @rxPaginate-pagination-text-color;
    button[disabled] {
      color: @rxPaginate-pagination-text-color;
    }
  }

  .pagination > li > div > ul {
    float: right;
    padding-left: 5px;
    button {
      padding: 0;
      border: 0;
      background: transparent;
      color: @blue-700;
    }
    span {
      float: none;
      background: transparent;
    }
    & li + li::before {
      content: " | ";
      margin: 0 1em;
    }
  }

  .pagination > .page-links > li > a:hover,
  .pagination > .page-links > li > span:hover,
  .pagination > .page-links > li > a:focus,
  .pagination > .page-links > li > span:focus {
    border-top: 2px solid @gray-300;

  }

  .pagination > .page-links > .active > a,
  .pagination > .page-links > .active > span,
  .pagination > .page-links > .active > a:hover,
  .pagination > .page-links > .active > span:hover,
  .pagination > .page-links > .active > a:focus,
  .pagination > .page-links > .active > span:focus {
    cursor: default;
    border-top-color: @black;
    color: @black;
    background: @white;
  }

  .pagination .disabled > span,
  .pagination .disabled > span:hover,
  .pagination .disabled > span:focus,
  .pagination .disabled > a,
  .pagination .disabled > a:hover,
  .pagination .disabled > a:focus {
    visibility: hidden;
  }

  .pagination-lg > li > a,
  .pagination-lg > li > span {
    padding: 10px 16px;
    font-size: 18px;
  }

  .pagination-sm > li > a,
  .pagination-sm > li > span {
    padding: 5px 10px;
    font-size: 12px;
  }
}

.loading-overlay {
  z-index: 100;
  position: absolute;
  top: -1px;
  left: -1px;
  width: ~"calc(100% + 2px)";
  height: ~"calc(100% + 2px)";
  background: rgba(250, 250, 250, 0.8); // == @gray-25 at 80%
