/*
 * rxSelect
 */
.rxSelect {
  z-index: 0; // creates local z-index stacking context
  .box-sizing(border-box);
  display: block;
  position: relative;
  color: @rxSelect-color;
  background: @rxSelect-background;
  .border-radius(@rxSelect-border-radius);

  // Element should be resizable via wrapper alone
  min-width: @rxSelect-min-width;
  height: @rxSelect-height;

  &.rx-disabled {
    color: @rxSelect-color-disabled;
    background: @rxSelect-background-disabled;
  }

  // position elements in wrapper
  select,
  .fake-select {
    .box-sizing(border-box);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .disabledSelect() {
    cursor: not-allowed;

    & + .fake-select {
      border-color: @rxSelect-border-color-disabled;
      border-width: 1px;

      .select-trigger {
        background: @rxSelect-background-disabled;
        border-left-width: 0px;
        border-left-color: @rxSelect-color-disabled;
        color: @rxSelect-trigger-color-disabled;
      }
    }
  }//.disabledSelect()

  .invalidSelect() {
    & + .fake-select {
      border-color: @rxSelect-border-color-invalid;
      border-width: 2px;

      .select-trigger {
        color: @rxSelect-trigger-color-invalid;
      }
    }

    &[disabled] {
      .rxSelect.disabledSelect();
    }
  }//.invalidSelect()

  select {
    z-index: 0;
    cursor: pointer;

    // reset native styling
    // !important is required to reset native styling and avoid any additional styling
    border: 0 !important; // lesshint importantRule: false
    outline: none !important; // lesshint importantRule: false
    background: transparent;
    &:-moz-focusring {
      // FF uses text color to draw ring. Make it transparent
      color: transparent;
      // However, we still need to display our select <option> text
      text-shadow: 0 0 0 @rxSelect-color;
    }

    & + .fake-select {
      border-color: @rxSelect-border-color;
      border-width: 1px;
    }

    &.ng-invalid.ng-dirty {
      .invalidSelect();
    }

    // Disabled Trumps All
    &[disabled] {
      .disabledSelect();
    }
  }//select

  .fake-select {
    z-index: 10;
    border: @rxSelect-border-width solid @rxSelect-border-color;
    overflow: hidden;
    .border-radius(@rxSelect-border-radius);
    display: flex;
    justify-content: flex-end;
    flex-flow: row nowrap;

    // This allow pointer events to pass THROUGH to the
    // native <select> element
    pointer-events: none !important; // lesshint importantRule: false

    // Custom trigger button will cover native <select> button
    .select-trigger {
      .box-sizing(border-box);
      width: @rxSelect-trigger-width;
      background: @rxSelect-trigger-background;
      color: @rxSelect-trigger-color;

      // perfectly center the trigger icon
      display: flex;
      align-items: center;
      justify-content: center;
    }//.select-trigger
  }//.fake-select
}//.rxSelect

/*
 * rxSelectFilter
 */
rx-select-filter {
  flex: 1;
  display: flex;
  flex-flow: row wrap;
  word-break: break-all;
}

/*
 * rxSortableColumn
 */
.rx-sortable-column {
  .btn-link {
    // This needs to be set explicitly, otherwise `.table-filters button`
    // applies a font size of 93% and sortable columns have smaller headers.
    font-size: 13px;
  }

  .sort-action {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    &:focus {
      // since we underline on focus, we can remove the outline rectangle that sticks around
      outline: none;
    }

    .display-value {
      flex: 1;
      text-align: left;
    }

    .sort-icon {
      flex: 0 0 16px;
      width: 13px;
      height: 13px;
      line-height: 13px;

      .bg {
        color: @gray-600;
      }
      .sort-direction-icon {
        color: @gray-900;
      }
    }
  }
}

@import "vars";

/*********************************************************
*  CSS3 Animation spinner
 *
*  Based on: http://cssdeck.com/labs/css3-animation-spinner
*  Copyright 2013 - birkof.ro
 *********************************************************/

.rx-spinner {
  background-color: transparent;
  border: 2px solid @gray-600;
  border-top-color: transparent;
  border-left-color: transparent;
  .border-radius(150px);
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-bottom: 3px;
  vertical-align: middle;
  -webkit-animation: spin 0.8s infinite linear;
  -moz-animation: spin 0.8s infinite linear;
  -ms-animation: spin 0.8s infinite linear;
  -o-animation: spin 0.8s infinite linear;
  animation: spin 0.8s infinite linear;
}

@-webkit-keyframes spin { /* Safari and Chrome */
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

@-moz-keyframes spin { /* Firefox */
  from {
    -moz-transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

