@import 'vars';

// Bar animations
// -------------------------
// WebKit
@-webkit-keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}

// Spec and IE10+
@keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}

// Bar itself
// -------------------------

.progress-bar-variant(@color) {
  background-color: @color;

  // Deprecated parent class requirement as of v3.2.0
  .progress-striped & {
    #gradient > .striped();
  }
}

// Outer container
.progress {
  overflow: hidden;
  height: @progressbar-line-height-computed;
  margin-bottom: @progressbar-line-height-computed;
  background-color: @progressbar-background-color;
  border: 1px solid @progressbar-border-color;
  padding: 1px;
}

// Bar of progress
.progress-bar {
  float: left;
  width: 0%;
  height: 100%;
  font-size: @progressbar-font-size;
  line-height: @progressbar-line-height-computed;
  color: @progressbar-default-text-color;
  text-align: center;
  background-color: @progressbar-default-background-color;
  .transition(width .6s ease);
}

// Striped bars
//
// `.progress-striped .progress-bar` is deprecated as of v3.2.0 in favor of the
// `.progress-bar-striped` class, which you just add to an existing
// `.progress-bar`.
.progress-striped .progress-bar,
.progress-bar-striped {
  #gradient > .striped();
  background-size: 40px 40px;
}

// Call animation for the active one
//
// `.progress.active .progress-bar` is deprecated as of v3.2.0 in favor of the
// `.progress-bar.active` approach.
.progress.active .progress-bar,
.progress-bar.active {
  .animation(progress-bar-stripes 2s linear infinite);
}

// Variations
// -------------------------

.progress-bar-secondary {
  .progress-bar-variant(@progressbar-secondary-background-color);
  color: @progressbar-secondary-text-color;
}

.progress-bar-tertiary {
  .progress-bar-variant(@progressbar-tertiary-background-color);
  color: @progressbar-tertiary-text-color;
}

#gradient {

  // Horizontal gradient, from left to right
  //
  // Creates two color stops, start and end, by specifying a color and position for each color stop.
  // Color stops are not available in IE9 and below.
  .horizontal(@start-color: @gray-800; @end-color: @gray-950; @start-percent: 0%; @end-percent: 100%) {
    background-image: -webkit-linear-gradient(left, @start-color @start-percent, @end-color @end-percent); // Safari 5.1-6, Chrome 10+
    background-image: -o-linear-gradient(left, @start-color @start-percent, @end-color @end-percent); // Opera 12
    background-image: linear-gradient(to right, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
    background-repeat: repeat-x;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)",argb(@start-color),argb(@end-color))); // IE9 and down
  }

  // Vertical gradient, from top to bottom
  //
  // Creates two color stops, start and end, by specifying a color and position for each color stop.
  // Color stops are not available in IE9 and below.
  .vertical(@start-color: @gray-800; @end-color: @gray-950; @start-percent: 0%; @end-percent: 100%) {
    background-image: -webkit-linear-gradient(top, @start-color @start-percent, @end-color @end-percent);  // Safari 5.1-6, Chrome 10+
    background-image: -o-linear-gradient(top, @start-color @start-percent, @end-color @end-percent);  // Opera 12
    background-image: linear-gradient(to bottom, @start-color @start-percent, @end-color @end-percent); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
    background-repeat: repeat-x;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)",argb(@start-color),argb(@end-color))); // IE9 and down
  }

  .directional(@start-color: @gray-800; @end-color: @gray-950; @deg: 45deg) {
    background-repeat: repeat-x;
    background-image: -webkit-linear-gradient(@deg, @start-color, @end-color); // Safari 5.1-6, Chrome 10+
    background-image: -o-linear-gradient(@deg, @start-color, @end-color); // Opera 12
    background-image: linear-gradient(@deg, @start-color, @end-color); // Standard, IE10, Firefox 16+, Opera 12.10+, Safari 7+, Chrome 26+
  }

  .horizontal-three-colors(@start-color: @blue-accent; @mid-color: @purple-500; @color-stop: 50%; @end-color: @red-700) {
    background-image: -webkit-linear-gradient(left, @start-color, @mid-color @color-stop, @end-color);
    background-image: -o-linear-gradient(left, @start-color, @mid-color @color-stop, @end-color);
    background-image: linear-gradient(to right, @start-color, @mid-color @color-stop, @end-color);
    background-repeat: no-repeat;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=1)",argb(@start-color),argb(@end-color))); // IE9 and down, gets no color-stop at all for proper fallback
  }

  .vertical-three-colors(@start-color: @blue-accent; @mid-color: @purple-500; @color-stop: 50%; @end-color: @red-700) {
    background-image: -webkit-linear-gradient(@start-color, @mid-color @color-stop, @end-color);
    background-image: -o-linear-gradient(@start-color, @mid-color @color-stop, @end-color);
    background-image: linear-gradient(@start-color, @mid-color @color-stop, @end-color);
    background-repeat: no-repeat;
    filter: e(%("progid:DXImageTransform.Microsoft.gradient(startColorstr='%d', endColorstr='%d', GradientType=0)",argb(@start-color),argb(@end-color))); // IE9 and down, gets no color-stop at all for proper fallback
  }

  .radial(@inner-color: @gray-800; @outer-color: @gray-950) {
    background-image: -webkit-radial-gradient(circle, @inner-color, @outer-color);
    background-image: radial-gradient(circle, @inner-color, @outer-color);
    background-repeat: no-repeat;
  }

  .striped(@color: rgba(255,255,255,.15); @angle: 45deg) {
    background-image: -webkit-linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);
    background-image: -o-linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);
    background-image: linear-gradient(@angle, @color 25%, transparent 25%, transparent 50%, @color 50%, @color 75%, transparent 75%, transparent);
  }
}

.animation(@animation) {
  -webkit-animation: @animation;
  -o-animation: @animation;
  animation: @animation;
}

@import (reference) "vars";

rx-date-picker {
  #vars.rxDatePicker();
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0;
  width: @control-width;

  .flexCentered() {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
  }//.flexCentered()

  &,
  &.ng-valid {
    .control {
      .flexCentered();
      background-color: @control-background-color;
      border: 1px solid @control-border-color;
      box-sizing: border-box;
      color: @control-text-color;
      cursor: @control-cursor;
      flex-flow: row nowrap;
      height: @control-height;
      padding: @control-padding-x @control-padding-y;

      .displayValue {
        flex: 1;
      }

      .icon {
        color: @control-icon-color;
        flex: 0 0 @control-icon-width;
        margin-left: @control-gutter;
      }
    }
  }

  &.ng-invalid.ng-dirty {
    .control {
      border: 2px solid @invalid-control-border-color;
      // MUST differ with valid padding by 1px to account for thicker border
      padding: (@control-padding-x - 1) (@control-padding-y - 1);

      .icon {
        color: @invalid-control-icon-color;
      }
    }
  }

  // Disabled styles should trump all other styles
  // !important is required and prevents additional styling
  &[disabled] {
    .control {
      background-color: @disabled-control-background-color !important; // lesshint importantRule: false
      border: 1px solid @disabled-control-border-color !important; // lesshint importantRule: false
      color: @disabled-control-text-color !important; // lesshint importantRule: false
      cursor: @disabled-control-cursor !important; // lesshint importantRule: false

      .icon {
        color: @disabled-control-icon-color !important; // lesshint importantRule: false
      }
    }
  }

  .popup {
    background-color: @popup-background-color;
    border-color: @popup-border-color;
    border-style: solid;
    border-width: 5px 1px 1px;
    border-top-color: @popup-border-top-color;
    margin: 0;
    padding: 0;
    position: absolute;
    width: @popup-width;
    z-index: @popup-z-index;

    .circle {
      .border-radius(100%);
      .flexCentered();
      border: 1px solid transparent;
      box-sizing: border-box;
      flex: 0 0 100%;
      height: 100%;
      width: 100%;
    }//.circle

    .day,
    header h6 {
      .flexCentered();
      flex: 0 0 @day-width;
      height: @day-width;
    }

    .day {
      background-color: transparent;
      color: @day-text-color;
      cursor: pointer;

      &.outOfMonth {
        background-color: @out-of-month-day-background-color;
        cursor: default;
      }

      .circle {
        background-color: transparent;
      }
    }//.day

    nav {
      align-items: stretch;
      border-bottom: 2px solid @popup-border-color;
      color: @nav-text-color;
      cursor: default;
      display: flex;
      flex-flow: row nowrap;
      font-size: @nav-font-size;
      font-weight: bold;
      height: @nav-height;
      justify-content: center;

      span {
        .flexCentered();
        box-sizing: border-box;

        &.arrow {
          color: @nav-arrow-icon-color;
          cursor: pointer;
          flex: 0 0 @nav-arrow-width;

          &:hover {
            color: @nav-arrow-hover-icon-color;
          }
        }

        &.currentMonth {
          flex: 1;
        }
      }//span
    }//nav

    .calendar {
      .flexCentered();
      flex-flow: row wrap;

      header {
        .flexCentered();
        background-color: @header-background-color;
        border-bottom: 1px solid @header-border-bottom-color;
        color: @header-text-color;
        font-size: @header-font-size;
        font-weight: bold;
        height: @header-height;
        flex-flow: row nowrap;
        flex: 0 0 100%;
      }//header

      .day {
        &.today {
          .circle {
            border-color: @today-border-color;
          }
        }//.today

        &.selected {
          .circle {
            background-color: @selected-day-background-color;
            border-color: transparent;
            color: @selected-day-text-color;
          }
        }//.selected

        &:not(.selected):hover {
          .circle {
            background-color: @day-hover-background-color;
            color: @day-hover-text-color;
          }
        }//not(.selected):hover
      }//.day
    }//.calendar
  }//.calendar-selector
}//rx-date-picker

